import React, { useState } from "react"
import QrReader from 'react-qr-reader'
import Resource, { QR2Info } from "../Resource";
import icon from "../SVG/Profile.svg";
import { Link } from "react-router-dom";
import CheckOutIcon from "../SVG/CheckOut.svg";
import CheckInIcon from "../SVG/CheckIn.svg";
import nextIcon from "../SVG/next.svg";



export default function () {
    const [state, setstate] = useState(null);
    let BussinessName = Resource.AppCaption;

    return (
        <div className="shadow overflow-hidden rounded-t-md mt-0 m-auto w-full" style={{
            backgroundColor: "#F7BE00",
            // width: "400px"
        }}>
            <div className="px-6 py-5 border-gray-200 flex flex-row">
                <div className="w-2/3">
                    <p className="mt-1  text-sm " style={{ color: "#A76000" }}>
                        {Resource.AppWelcome}
                    </p>
                    <h3 className="text-lg leading-6 font-medium text-white">
                        {state || BussinessName}
                    </h3>
                </div>

                <img src={icon} className="w-32 h-32 relative top-0 right-0 -mb-10" />

            </div>
            <div className="bg-white rounded-t-full h-5 -mb-1">
            </div>
            <div className="bg-white flex h-64 flex-col px-4 h-screen">

                {!state && < QrReader
                    delay={300}
                    showViewFinder={true}
                    onError={(err) => {
                        console.error(err)
                    }}
                    onScan={(data) => {
                        if (data) {

                            const info = QR2Info(data);
                            console.log(data, info);
                            setstate(info);
                        }
                    }}
                    className="w-64 h-64 mx-auto"
                />}

                {state &&
                    <>
                        <Link to={"atshop?action=CheckIn&b=" + state}
                            className="mx-auto px-4 py-4 text-center rounded-lg flex flex-row justify-center w-64 mt-8"
                            style={{
                                backgroundColor: Resource.buttonColor.CheckIn
                            }}>
                            <img src={CheckInIcon} className="w-16 h-16" />
                            <div className="flex flex-col  w-3/5">
                                <span className="text-2xl text-white">
                                    {Resource.CheckIn}
                                </span>
                                <span className="text-xs text-green-800 font-light">
                                    {Resource.CheckInTooltip}
                                </span>

                            </div>
                            <img src={nextIcon} className="w-4 h-full" />

                        </Link>
                        <Link to={"atshop?action=CheckOut&b=" + state} className="mx-auto px-4 py-4 text-center rounded-lg flex flex-row justify-center w-64 mt-2"
                            style={{
                                backgroundColor: Resource.buttonColor.CheckOut
                            }}>
                            <img src={CheckOutIcon} className="w-16 h-16" />

                            <div className="flex flex-col  w-3/5">
                                <span className="text-2xl text-white">
                                    {Resource.CheckOut}
                                </span>
                                <span className="text-xs text-red-800 font-light">
                                    {Resource.CheckInTooltip}
                                </span>

                            </div>
                            <img src={nextIcon} className="w-4 h-full" />
                        </Link>
                    </>
                }
            </div>
        </div>
    )
}