import React, { useState } from "react"

import {
    Link
} from "react-router-dom";
import MyStore from "./Pages/MyStore";

const Menu = [];

Menu.push({ caption: "atshop", link: "atshop" });
Menu.push({ caption: "Scan QR", link: "ScanQR" });
Menu.push("-");
Menu.push({ caption: "My Store", link: "MyStore" });
Menu.push({ caption: "Store QR", link: "StoreQR" });
Menu.push("-");
Menu.push({ caption: "History", link: "History" });
Menu.push("-");


export default function () {
    const [state, setstate] = useState(false);

    return (
        <nav className="bg-gray-800">
            <div className="max-w-7xl mx-auto px-2 sm:px-6 lg:px-8">
                <div className="relative flex items-center justify-between h-16">
                    <div className="flex-1 flex items-center justify-center sm:items-stretch sm:justify-start">
                        <div className="flex-shrink-0 text-white text-2xl">
                            Logo
                        </div>
                    </div>
                    <div className="absolute inset-y-0 right-0 flex items-center pr-2 sm:static sm:inset-auto sm:ml-6 sm:pr-0">
                        <div className="ml-3 relative">
                            <div>
                                <button className="flex text-sm border-2 border-transparent rounded-full focus:outline-none focus:border-white transition duration-150 ease-in-out"
                                    onClick={() => {
                                        setstate(!state)
                                    }}>
                                    <img className="h-8 w-8 rounded-full" src="data:image/svg+xml;base64," alt="" />
                                </button>
                            </div>

                            {state &&
                                <div className="origin-top-right absolute right-0 mt-2 w-48 rounded-md shadow-lg">
                                    <div className="py-1 rounded-md bg-white shadow-xs" role="menu" aria-orientation="vertical" aria-labelledby="user-menu">

                                        {Menu.map((menuItem) => {
                                            let linkText = menuItem;
                                            let linkTo = "";
                                            let breakLine = false;

                                            if (linkText === "-") {
                                                breakLine = true;
                                            } else {
                                                linkText = menuItem.caption;
                                                linkTo = menuItem.link;

                                            }

                                            return (
                                                <>
                                                    {!breakLine && <Link to={linkTo} key={linkTo} className="block px-4 py-2 text-sm  text-gray-700 hover:bg-gray-100 focus:outline-none focus:bg-gray-100 transition duration-150 ease-in-out">{linkText}
                                                    </Link>}
                                                    {breakLine &&
                                                        <div className="w-full h-0 border" />}
                                                </>
                                            )
                                        })}

                                        <Link to="atshop?action=CheckIn"
                                            key={"signout"}
                                            className="block px-4 py-2 text-sm  text-gray-700 hover:bg-gray-100 focus:outline-none focus:bg-gray-100 transition duration-150 ease-in-out" role="menuitem">Check In
            </Link>
                                        <Link to="atshop?action=CheckOut"
                                            key={"signout"}
                                            className="block px-4 py-2 text-sm  text-gray-700 hover:bg-gray-100 focus:outline-none focus:bg-gray-100 transition duration-150 ease-in-out" role="menuitem">Check Out
            </Link>

                                        <a href="#"
                                            key={"signout"}
                                            className="block px-4 py-2 text-sm  text-gray-700 hover:bg-gray-100 focus:outline-none focus:bg-gray-100 transition duration-150 ease-in-out" role="menuitem">Sign out
            </a>
                                    </div>
                                </div>
                            }
                        </div>
                    </div>
                </div>
            </div>

        </nav>
    )
}  