import React, { useState } from 'react';
import ReactDOM from 'react-dom';
// import App from './App';
import * as serviceWorker from './serviceWorker';
import './styles/tailwind.css';

import {
  HashRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import Header from "./Header";

import History from "./Pages/History";
import AtShop from "./Pages/AtShop";
import AtShopDone from "./Pages/AtShopDone";

import MyStore from "./Pages/MyStore";
import ScanQR from "./Pages/ScanQR";
import StoreQR from "./Pages/StoreQR";
import NotFound from "./Pages/NotFound";
import icon from "./SVG/surface.svg"
import Resource from './Resource';


ReactDOM.render(
  <React.StrictMode>
    <Router>
      <div className="w-full h-screen m-auto bg-gray-100 static font-sans overflow-hidden">
        {/* <Header /> */}
        <div className="w-full flex flex-col">
          <Switch>
            <Route exact path="/">
              <ScanQR />
            </Route>
            <Route exact path="/atshop">
              <AtShop />
            </Route>
            <Route exact path="/atshopdone">
              <AtShopDone />
            </Route>

            <Route path="/MyStore">
              <MyStore />
            </Route>
            <Route path="/ScanQR">
              <ScanQR />
            </Route>
            <Route path="/StoreQR">
              <StoreQR />
            </Route>
            <Route path="/History">
              <History />
            </Route>
            <Route path="/MyCheck-in">
              <History />
            </Route>
            <Route path="*">
              <NotFound />
              {/* <NoMatch title="Your are lost !" subtitle="Minilabz Oy" /> */}
            </Route>
          </Switch>
        </div>
        <footer className="bg-gray-200 py-2 flex flex-row justify-center mt-4 absolute bottom-0 w-full">
          <img src={icon} className="w-10 h-10 ml-2 my-auto"></img>
          <div className="text-xs w-4/5 border px-4 font-light">
            {Resource.Endosed}<span className="underline">{Resource.Endosedby}</span>
            <br />
            {Resource.Powered} <span className="underline">{Resource.Poweredby}</span>
          </div>
          <span className="text-xs text-center text-blue-500 py-2 px-4 underline">
            {Resource.LearnMore}
          </span>
        </footer>
      </div>
    </Router>
  </React.StrictMode >,
  document.getElementById('root')
);


// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
