export default Resource;

const QR_PREFIX = "content:";
export function getServer() {
    return window.location + "/#/?";
}

export function Info2QR(bussinesInfo) {
    return QR_PREFIX + getServer() + bussinesInfo
}
export function QR2Info(qrvalue) {
    return qrvalue
        .replace(QR_PREFIX, "")
        .split("?")
        .slice(-1);

}