import React, { useState } from "react"
import QRCode from "qrcode.react";
import { QR2Info } from "../Resource";

const Value = localStorage.getItem("MyBussinessQR") || "content:http://localhost:3000/#/?XXXX:ABCNAME";

const info = QR2Info(Value);

function DownloadQR() {
    const canvas = document.getElementById("myStoreQr");
    const pngUrl = canvas
        .toDataURL("image/png")
        .replace("image/png", "image/octet-stream");
    let downloadLink = document.createElement("a");
    downloadLink.href = pngUrl;
    downloadLink.download = Value + ".png";
    document.body.appendChild(downloadLink);
    downloadLink.click();
    document.body.removeChild(downloadLink);
}

export default function () {

    return (
        <div className="w-3/5 m-auto mt-10 bg-white shadow overflow-hidden sm:rounded-lg">
            <div className="px-4 py-5 border-b border-gray-200 sm:px-6">
                <h3 className="text-lg leading-6 font-medium text-gray-900">
                    My Store QR
    </h3>
                <p className="mt-1  text-sm  text-gray-500">
                    Ask customer to scan this QR Code.
    </p>
            </div>

            <div className="flex justify-center m-10">
                {info}
                <br />
                <QRCode
                    id="myStoreQr"
                    value={Value}
                    size={256}
                    bgColor={"#ffffff"}
                    fgColor={"#000000"}
                    level={"M"}
                    includeMargin={true}
                />

            </div>
            <div className="px-4 py-5 border-t border-gray-200 sm:px-6 justify-center flex space-x-4">

                <button className="bg-indigo-600 text-lg text-center text-white px-4 py-2 rounded-sm shadow" onClick={DownloadQR}>Download QR</button>

            </div>
        </div>
    )
}